import React from "react";
import Accordion from "react-bootstrap/Accordion";
import Card from "react-bootstrap/Card";
import { useRef } from "react";
import { motion, useCycle } from "framer-motion";
import gibush_stage from "./assets/img/gibushupdated.svg";
import tironut from "./assets/img/bucket-hat-sketch.png";
import gdud from "./assets/img/gdud.png";
import imun from "./assets/img/imunmitkadem.svg";
import maof from "./assets/img/sketch-book.png";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import ElaborateProcess from "./ElaborateProcess";

class Proccess extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      divToShow: 1,
    };
    this.changeDivTo = this.changeDivTo.bind(this);
  }

  changeDivTo = (clickedStage) => {
    this.setState({ divToShow: clickedStage });
  };

  render() {
    return (
      <motion.div className="m-auto proccess" style={{ padding: "5vh" }}>
        <Row style={{ textAlign: "center", color: "#771913" }}>
          <motion.div animate={{ scale: 1.25 }} className="m-auto">
            <Col onClick={this.changeDivTo.bind(this, 1)}>
              <Row className="m-auto">
                <img
                  src={gibush_stage}
                  className="item_process item_process_gibush"
                  style={{ transform: "rotate(20deg)" }}
                />
              </Row>
              <Row className="m-auto">
                <p>גיבוש</p>
              </Row>
            </Col>
          </motion.div>
          <motion.div className="m-auto">
            <Col onClick={this.changeDivTo.bind(this, 2)}>
              <img src={tironut} className="item_process" />
              <Row className="m-auto">
                <p>טירונות</p>
              </Row>
            </Col>
          </motion.div>
          <motion.div className="m-auto">
            <Col onClick={this.changeDivTo.bind(this, 3)}>
              <img src={imun} className="item_process" />
              <Row>
                <p>אימון מתקדם</p>
              </Row>
            </Col>
          </motion.div>
          <motion.div className="m-auto">
            <Col onClick={this.changeDivTo.bind(this, 4)}>
              <img src={gdud} className="item_process" />
              <Row className="m-auto">
                <p>גדוד</p>
              </Row>
            </Col>
          </motion.div>
          <motion.div className="m-auto">
            <Col onClick={this.changeDivTo.bind(this, 5)}>
              <img
                src={maof}
                className="item_process"
                style={{ padding: "1vh" }}
              />
              <Row className="m-auto">
                <p>פרק מעו"ף / משימה</p>
              </Row>
            </Col>
          </motion.div>
        </Row>
        <Row>
          <ElaborateProcess divToShow={this.state.divToShow} />
        </Row>
      </motion.div>
    );
  }
}

export default Proccess;
