import React from "react";
import Accordion from "react-bootstrap/Accordion";
import Card from "react-bootstrap/Card";
import { useRef } from "react";
import { motion, useCycle } from "framer-motion";
import gibush_stage from "./assets/img/gibushupdated.svg";
import tironut from "./assets/img/tironut.svg";
import gdud from "./assets/img/gdud.png";
import imun from "./assets/img/imunmitkadem.svg";
import maof from "./assets/img/maof_stage.svg";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";

function ElaborateProcess(props) {
  let showDiv = {
    display: "block",
  };
  let hideDiv = {
    display: "none",
  };

  return (
    <motion.div className="m-auto elaborated-process">
      <motion.div
        id="gibush_stage"
        style={props.divToShow === 1 ? showDiv : hideDiv}
      >
        <h5>גיבוש</h5>
        <p>
          לאחר ההרשמה למסלול, תזומנו ליום/ימי גיבוש, ימים שנועדו לבחון את ההתאמה
          שלכם לשירות ביחידה.
          <br />
          במהלך אותו תעשו פעילויות המדמות יום בחייו של הלוחם: ריצות, זחילות,
          טיפוס, משמעת וכן הלאה.
          <br />
          בסיום אותו יום, נחתים אתכם על עסקה עם השטן.
        </p>
      </motion.div>
      <motion.div
        id="tironut_stage"
        style={props.divToShow === 2 ? showDiv : hideDiv}
      >
        <h5>טירונות</h5>
        1. לאחר שלב הגיוס החיילים מתחילים את שלב הטירונות בבא"ח (בסיס אימונים
        חטיבתי) בו הם לומדים את המערכת הצבאית, שימוש במגוון כלי נשק, כושר גופני
        ועוד. במהלך הטירונות החיילים יעברו טקס השבעה ויהיו זכאים לתג החטיבה שבה
        הם משרתים. בסיום הטירונות הלוחמים יוסמכו ללוחמים ברמת רובאי 05, ולוחמי
        מגיני הנגב יוסמכו לרובאי 03.
        <br />
        2. האימונים בטירונות כוללים:
        <br />
        -שימוש בכלי נשק שונים - נשק קל, נגב, מאג ורימון יד.
        <br />
        -עזרה ראשונה - הלוחם יוסמך כמע"ר (מגיש עזרה ראשונה).
        <br />
        -קורס חובשים קרביים - לאחר מיון והתאמה, מספר הקצאות מוגבל.
        <br />
        -שימוש במכשירי קשר.
        <br />
        -התגוננות מאיום אב"כ.
        <br />
        -אימונים גופניים - בחני ריצה ובוחן מסלול מכשולים.
        <br />
        -תרגילי פרט בירי-חוליה (תרגולי הסתערות ומטווחים - לבד ובצוות).
        <br />
        -שדאות.
        <br />
        -ניווטים ומסעות.
        <br />
        -לש"ב. (לוחמה בשטח בנוי)
      </motion.div>
      <motion.div
        id="imun_mitkadem"
        style={props.divToShow === 3 ? showDiv : hideDiv}
      >
        <h5>אימון מתקדם</h5>
        <p>
          1. לאחר סיום הטירונות הלוחמים עוברים לשלב האימון המתקדם שנמשך כ-3
          חודשים, בו הם רוכשים מיומנות לחימה אישית גבוהה, כישורי לחימה בשטחים
          בנויים ומורכבים, לפני סיום שלב האימון המתקדם הלוחמים יבצעו את מסע
          הכומתה המפורסם, אחריו הלוחמים יהיו זכאים לחבוש את הכומתה של היחידה בה
          הם נמצאים. לוחמים אשר יוכיחו כישורי פיקוד גבוהים יצאו לקורס מפקדי כיתה
          בתום ההכשרה. בסיום האימון המתקדם הלוחמים יוסמכו ללוחמים ברמת רובאי 07.
          <br />
          2. התמחויות באימון מתקדם כוללות:
          <br />
          -קרב מגע.
          <br />
          -כיבוש יעד מבוצר.
          <br />
          -מפקד כיתה - תחילתו של ציר הפיקוד בצה"ל, מתאים לחיילים עם יכולות
          פיקוד.
          <br />
          -תרגילי ירי ברמת הכיתה, מחלקה, פלוגה וגדוד.
          <br />
          -לוחמה מתקדמת בשטחים בנויים ומורכבים.
        </p>
      </motion.div>

      <motion.div id="gdud" style={props.divToShow === 4 ? showDiv : hideDiv}>
        <h5>תקופת המסלול בגדוד</h5>
        1. לאחר מסע ההכשרה הלוחמים יבצעו שירות קרבי בגדוד ובו יהיו אחראים על
        ביטחון האזרחים בשטח שמעבר לקו הירוק ויתמודדו למול איומי הטרור.
        <br />
        2. במהלך המסלול הלוחמים המתאימים יעברו קורסי הכשרה מקצועיים בתחומים
        שונים כגון קורס צלפים, תצפית, נהיגה מבצעית ועוד.
        <br />
        3. לאחר ארבעה חודשים של פעילות מבצעית, הלוחמים יקבלו את סיכת לוחם הגדוד.
      </motion.div>

      <motion.div
        id="maof_stage"
        style={props.divToShow === 5 ? showDiv : hideDiv}
      >
        <h5>פרק המעו"ף (מצוינות, עתיד ופיתוח אישי)</h5>
        <p>
          כעבור שנתיים של שירות פעיל מיום הגיוס, זכאים חיילי חרב וחרב תומכ"ל
          לתקופת לימודים.
          <br />
          בזמן זה, שנמשך שמונה חודשים, ישולבו החיילים באחד משלושה מסלולים, לטובת
          שילובם התעסוקתי בעתיד באזרחות ובצבא:
          <br />
          מכינה אקדמית: לימודי מכינה המאפשרים השתלבות במסלול האקדמיה לרכישת
          תארים מתקדמים.
          <br />
          מסלול הנדסאים: במסגרת מסלול הנדסאים, מתאפשר לחייל ללמוד לתעודת הנדסאי
          חשמל או בניין.
          <br />
          קורסים מקצועיים: מגוון קורסים במקצועות החשמל, אלקטרוניקה ומחשוב שיעזרו
          להשתלב באופן מידי בשוק התעסוקה.
        </p>
      </motion.div>
    </motion.div>
  );
}

export default ElaborateProcess;
